export default {
	data() {
		return {
			InputBottom: 0,
			TabCur: 0,
			scrollLeft: 0,
			navList: ["逻辑", "数学", "英语", "写作"]
		}
	},
	methods: {
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		}
	}
}
